import React from "react";

class ProductListing extends React.Component{
	render() {
		return(
			<div className="product">
				<h2>Product Listing</h2>
				<div className="product-tile">
					<div className="product-tile__upper-section">
						<div className="product-tile__img-wrapper">
							<img src="" alt="" />
						</div>
					</div>
					<div className="product-tile__lower-section">
						<div className="product-tile__title">title</div>
						<div className="product-tile__price">{300}</div>
						<div className="product-tile__swatches">swatches</div>
					</div>
				</div>
			</div>
		);
	}
}

export default ProductListing;